.navigationBar {
    background-color: #2E3131;
    height: 50px;
    width: 100%;
    position: fixed;
}

.navigationBar ul {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navigationBar li {
    padding: 0 16px;
    list-style: none;
}
.navigationBar a {
    width: 60px;
    height: 30px;
    box-sizing: border-box;
    display: block;
    color: #F5F5F5;
    text-align: center;
    text-decoration: none;
    font-size: 20px;
}
.navigationBar a.active {
    border-bottom: 2px solid #F5F5F5;
}