.languageSelection {
    height: 30px;
    border-radius: 4px;
    border: 2px solid #2E3131;
    box-sizing: border-box;
    display: flex;
}
.languageSelection label {
    width: 100%;
    height: 100%;
}
.languageSelection input {
    display: none;
}

.checkbox {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    height: 100%;
}

.languageSelection input:checked + .checkbox {
    color: #f5f5f5;
    background-color: #2E3131;
}
