@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
* {
    font-family: 'Roboto', sans-serif;
    padding: 0;
    margin: 0;
}

h1 {
    text-align: center;
}
.app {
    background-color: #F5F5F5;
    color: #2E3131;
}
.main {
    box-sizing: border-box;
}
.main>div {
    height: 100%;
    min-height: 100vh;
    padding: 60px 10px 10px;
}





.home {
    height: 100vh;
    background-image: url(/swn/static/media/Reynisfjara.7bf5dcda.jpg);
    background-size: cover;
    background-position: center;
}
.nouns {
}

.verbs {
}

.listOfVerbs {
    width: 100%;
}

.listOfVerbs ul {
    list-style: none;
}

.listOfVerbs li {
    font-size: 24px;
    margin: 10px;
    display: flex;
    justify-content: center;
}
.languageSelection {
    height: 30px;
    border-radius: 4px;
    border: 2px solid #2E3131;
    box-sizing: border-box;
    display: flex;
}
.languageSelection label {
    width: 100%;
    height: 100%;
}
.languageSelection input {
    display: none;
}

.checkbox {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    height: 100%;
}

.languageSelection input:checked + .checkbox {
    color: #f5f5f5;
    background-color: #2E3131;
}

.test {

}

.header {
    position: absolute;
    width: 100%;
}
.navigationBar {
    background-color: #2E3131;
    height: 50px;
    width: 100%;
    position: fixed;
}

.navigationBar ul {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navigationBar li {
    padding: 0 16px;
    list-style: none;
}
.navigationBar a {
    width: 60px;
    height: 30px;
    box-sizing: border-box;
    display: block;
    color: #F5F5F5;
    text-align: center;
    text-decoration: none;
    font-size: 20px;
}
.navigationBar a.active {
    border-bottom: 2px solid #F5F5F5;
}
